
.divMax[data-v-c123438e]{
    background-color: #F6F7FB;
}
.welcomeText[data-v-c123438e]{
    position: fixed;
    top:20%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.divMenuShow[data-v-c123438e]{
    position: absolute;
    top:50%;
    left:10%;
    right:10%;
    height:200px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    overflow: hidden;
    -webkit-box-shadow: 0px 0px 10px #222;
            box-shadow: 0px 0px 10px #222;
}
.menuList[data-v-c123438e]{
    width:100%;
    height:100%;
    background-color: #FFF;
}
.Btn[data-v-c123438e]{
    position: absolute;
    top:0;
    bottom: 0;
    width:10%;
    min-width: 48px;
    z-index:1;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #eee;
    cursor: pointer;
    background-image: url(/static/img/icon/ArrowRight.png);
    /*opacity: 0.2;*/
}
.leftBtn[data-v-c123438e]{
    left:0;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.rightBtn[data-v-c123438e]{
    right:0;
}
.menuList li[data-v-c123438e]{
    float: left;
    width:100%;
    height:100%;
}
.menuList li > a[data-v-c123438e]{
    display: block;
    width:100%;
    height:100%;
    color: rgb(0, 136, 186);
    font-size: 22px;
    /*background-image:linear-gradient(to right,rgb(0, 136, 186),rgb(0, 136, 186));*/
}
.menuList li > a > span[data-v-c123438e]{
    display: block;
    position: relative;
    top:50%;
    left:50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    text-align: center;
    max-width: 60%;
    text-overflow: ellipsis;
}



