
.divMax[data-v-b559d194]{
    height: 100%;
    widows: 100%;
}
.menuBox[data-v-b559d194]{
    position: fixed;
    z-index: 101;
    top:0;
    bottom: 0;
    left:0;
    width:20%;
    background-color: #393939;
    opacity: 0.9;
    padding:30px 0;
    overflow: hidden;
}
.menuBox > a[data-v-b559d194]{
    display: block;
    text-align: center;
    padding:10px 0;
    height:50px;
    overflow: hidden;
    white-space: nowrap;
    color:white;
}
.menuBox > a[data-v-b559d194]:hover{
    background-color: #aaa;
    color:rgb(255, 242, 184);
}
.menuBox > a > img[data-v-b559d194]{
    width:32px;
    height:32px;
}
@media screen and (min-width: 960px) {
.menuBox > a > img[data-v-b559d194]{
        display: inline-block;
}
.menuBox > a > span[data-v-b559d194]{
        position: relative;
        top:-9px;
        display: inline-block;
        width:100px;
        line-height: 32px;
}
}
@media screen and (max-width: 960px){
.menuBox > a > span[data-v-b559d194]{
        display: none;
}
}
.menuSlide[data-v-b559d194]{
    position: fixed;
    z-index: 101;
    top:0;
    bottom: 0;
    left:0;
    width:30px;
    /*background-size: 26px;*/
    background:#000 center url(/static/img/icon/right.png) no-repeat;
    opacity: 0.3;
    cursor: pointer;
}
.menuSlide-on[data-v-b559d194]{
    left:20%;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
