.routerView[data-v-b559d194] {
    width:100%;
    height: 100%;
    overflow: hidden;
    /*padding-left:30px;*/
}
.compTitle[data-v-b559d194] {
    position: absolute;
    top: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    line-height: 32px;
    color: rgb(0, 136, 186);
}
.menu-enter-active[data-v-b559d194]{
    -webkit-animation: menu-in-data-v-b559d194 0.5s;
            animation: menu-in-data-v-b559d194 0.5s;
}
@-webkit-keyframes menu-in-data-v-b559d194 {
0%{
        max-height:0;
        overflow: hidden;
}
100%{
        max-height: 130px;
        overflow: hidden;
}
}
@keyframes menu-in-data-v-b559d194 {
0%{
        max-height:0;
        overflow: hidden;
}
100%{
        max-height: 130px;
        overflow: hidden;
}
